
html,body {
    overflow-x: hidden;
    overflow-y: scroll;
}

body {
    overflow-y: visible;
}

@font-face {
    font-family: 'Avenir';
    font-weight: 400;
    src: local('Avenir'), url(./fonts/Avenir\ Regular.ttf) format('opentype');
  }
  @font-face {
    font-family: 'Avenir';
    font-weight: 900;
    src: local('Avenir'), url(./fonts/Avenir\ Heavy.ttf) format('opentype');
}
@font-face {
    font-family: 'Avenir';
    font-weight: 300;
    src: local('Avenir'), url(./fonts/Avenir\ Light.ttf) format('opentype');
}


@tailwind base;
@tailwind components;
@tailwind utilities;
@layer base {
    
    h3{
        @apply text-[clamp(.7em,1.5vw,2vh)]
    }
}
@layer components {
    .sticky-pos {
        position: -webkit-sticky;
        position: sticky;
    }
    .icon-xl {
        @apply
        w-[64px] xs:!w-[24px] 2xl:!w-[70px] xl:!w-[70px] lg:!w-[52px] sm:!w-[48px]
    }
    .icon-lg {
        @apply
        sm:w-[28px] md:!w-[52px] xs:!w-[20px] lg:!w-[46px] xl:!w-[52px] 2xl:!w-[64px]
    }
    .icon-md {
        @apply
        sm:w-[24px] xs:!w-[18px] md:!w-[52px]  lg:!w-[38px] xl:!w-[52px] 2xl:!w-[64px] 
    }
    .icon-sm {
        @apply
        sm:w-[22px] md:!w-[52px] xs:!w-[16px] lg:!w-[42px] xl:!w-[52px] 2xl:!w-[64px]
    }

    .font-body {
        @apply
        md:text-base xs:text-sm sm:!text-base xl:!text-lg 2xl:!text-xl lg:!text-base
        xs:leading-normal
        sm:leading-normal 
        font-serif
    }
    .font-headline-2 {
        @apply
        font-serif
        sm:!text-lg xs:text-base xl:!text-xl 2xl:!text-3xl lg:!text-lg
        tracking-wider
        !font-light
    }
    .font-headline-1 {
        @apply
        font-serif
        md:!text-3xl sm:!text-2xl xs:text-xl xl:!text-4xl 2xl:!text-6xl lg:!text-2xl
        font-bold
        leading-tight
        tracking-wide
    }
    
    .font-label-lg{
        @apply 
        font-serif 
        text-base sm:text-base xs:!text-xs xl:!text-base 2xl:!text-xl lg:!text-sm 
        tracking-wider
        leading-loose
    }
    .font-label-md{
        @apply
        font-serif 
        sm:text-base xl:!text-sm 2xl:!text-lg lg:!text-xs xs:text-xs
        tracking-wider
        leading-tight
    }
    .font-label-sm {
        @apply
        font-serif
        text-sm
        xs:text-xs
        tracking-wider
        leading-tight
    }
    .icon-hover {
        @apply
        hover:fill-neutral-90
        fill-neutral-50
        dark:fill-neutral-90
        dark:hover:fill-neutral-10
        hover:bg-primary-90

    }
    .p {
        @apply text-primary-20 dark:text-primary-95
            text-base
            xs:leading-normal xs:tracking-wider
            sm:leading-relaxed sm:tracking-wide
            md:leading-[43px]
            lg:leading-[55px]
            xl:leading-[62px]
            2xl:leading-[80px]
            xs:!p-2 xs:shadow-sm
            sm:text-left
            shadow-md
            dark:bg-primary-10 bg-primary-95 !bg-opacity-50
            p-8
            rounded-lg
    }
    .fixed-middle {
        @apply fixed left-1/2 transform -translate-x-1/2

    }
    .grids-layer-0 {
        @apply grid 
        xs:grid-cols-4 xs:gap-4 xs:mx-4 xs:px-0
        sm:gap-4 sm:!mx-8 sm:gap-4 
        md:grid-cols-12 md:gap-3
        lg:grid-cols-12 lg:gap-4  lg:mx-auto 
        xl:grid-cols-12 xl:gap-4  xl:mx-auto 
        2xl:grid-cols-12 2xl:gap-4  2xl:mx-auto 
    }
    .grids-layer-1 {
        @apply mx-auto
        lg:col-span-8 lg:col-start-3
        xl:col-span-8 xl:col-start-3
        2xl:col-span-6 2xl:col-start-4 2xl:col-end-10
        md:col-span-10 md:col-start-2
        xs:col-span-4 xs:py-0
        sm:py-0 sm:col-span-8 sm:float-right
    }

}
:root {

    --surface-variant:theme('colors.neutralVariant.90');
    --on-surface-variant:theme('colors.neutralVariant.30');
    --on-surface:theme('colors.neutral.10');
    --surface:theme('colors.neutral.99');
    --outline-variant: theme('colors.neutralVariant.80');
    --outline: theme('colors.neutralVariant.50');
    --on-background: theme('colors.primary.10');
    --background: theme('colors.primary.99');
    --on-primary-container:theme('colors.primary.10');
    --primary-container:theme('colors.neutral.90');
    --on-primary:theme('colors.primary.100');
    --primary:theme('colors.primary.40');
    --secondary:theme('colors.secondary.40');
    --on-secondary:theme('colors.secondary.100');
    --secondary-container:theme('colors.secondary.90');
    --on-secondary-container:theme('colors.secondary.10');

}
:root.dark {
    --surface-variant:theme('colors.neutralVariant.30');
    --on-surface-variant:theme('colors.neutralVariant.80');
    --on-surface:theme('colors.neutral.90');
    --surface:theme('colors.neutral.10');
    --outline-variant:theme('colors.neutralVariant.30');
    --outline:theme('colors.neutralVariant.60');
    --on-background:theme('colors.primary.99');
    --background:theme('colors.primary.5');
    --on-primary-container:theme('colors.primary.90');
    --primary-container:theme('colors.neutral.30');
    --on-primary:theme('colors.neutral.100');
    --primary:theme('colors.primary.80');
    --secondary:theme('colors.secondary.100');
    --on-secondary:theme('colors.secondary.40');
    --secondary-container:theme('colors.secondary.10');
    --on-secondary-container:theme('colors.secondary.90');
}

.on-primary {
    color: var(--on-primary);
}
.primary-container {
    background-color: var(--primary-container);
}
.background {
    background-color: var(--background);
}
.on-background {
    color: var(--on-background);
}
.primary{
    background-color: var(--primary);
}
.secondary{
    background-color: var(--secondary);
}


.outline {
    border-color: var(--outline);
}
.outline-variant {
    border-color: var(--outline-variant);
}

.neutral-variant {

    --neutral-variant-0: #ffff;
    --neutral-variant-10: #f7f7f7;
    --neutral-variant-20: #eeeeee;
    --neutral-variant-30: #e6e6e6;
    --neutral-variant-40: #cccccc;
    --neutral-variant-50: #999999;
    --neutral-variant-60: #808080;
    --neutral-variant-70: #666666;
    --neutral-variant-80: #4d4d4d;
    --neutral-variant-90: #333333;
    --neutral-variant-95: #606060;
    --neutral-variant-99: #1a1a1a;
    --neutral-variant-100: #000000;
}





